<template>
  <div class="home">
    <div class="feature-card">
      <router-link to="/movie/tt2975590">
        <img
          src="https://m.media-amazon.com/images/M/MV5BYThjYzcyYzItNTVjNy00NDk0LTgwMWQtYjMwNmNlNWJhMzMyXkEyXkFqcGdeQXVyMTQxNzMzNDI@._V1_SX300.jpg"
          alt="superman poster"
          class="featured-img"
        />
        <div class="detail">
          <h3>Batman v Superman</h3>
          <p>
            The general public is concerned over having Superman on their planet
            and letting the "Dark Knight" - Batman - pursue the streets of
            Gotham. While this is happening, a power-phobic Batman tries to
            attack Superman. Meanwhile, Superman tries to settle on a decision,
            and Lex Luthor, the criminal mastermind and millionaire, tries to
            use his own advantages to fight the "Man of Steel".
          </p>
        </div>
      </router-link>
    </div>
    <form @submit.prevent="SearchMovies()" class="search-box">
      <input
        type="text"
        placeholder="What are you looking for ?"
        v-model="search"
      />
      <input type="submit" value="Search" />
    </form>

    <div class="movies-list">
      <div class="movie" v-for="movie in movies" :key="movie.imdbID">
        <router-link :to="'/movie/' + movie.imdbID" class="movie-link">
          <div class="product-image">
            <img :src="movie.Poster" alt="Movie Poster" />
            <div class="type">{{ movie.Type }}</div>
          </div>
          <div class="detail">
            <p class="year">{{ movie.Year }}</p>
            <h3>{{ movie.Title }}</h3>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import env from "@/env.js";

export default {
  setup() {
    const search = ref("");
    const movies = ref([]);

    const SearchMovies = () => {
      if (search.value != "") {
        // console.log(search.value);
        fetch(`http://www.omdbapi.com/?apikey=${env.apikey}&s=${search.value}`)
          .then((response) => response.json())
          .then((data) => {
            //api裡的Search部分
            movies.value = data.Search;
            //search是指搜尋欄
            search.value = "";
            console.log(data);
          });
      }
    };

    return {
      search,
      movies,
      SearchMovies,
    };
  },
};
</script>

