<template>
  <div class="movie-bg">
    <div class="movie-detail">
      <h2>{{ movie.Title }}</h2>
      <p>{{ movie.Year }}</p>
      <img :src="movie.Poster" alt="Movie Poster" class="featured-img" />
      <h3>Plot</h3>
      <p>{{ movie.Plot }}</p>
    </div>
  </div>
</template>

<script>
import { ref, onBeforeMount } from "vue";
import { useRoute } from "vue-router";
import env from "@/env.js";

export default {
  setup() {
    const movie = ref({});
    const route = useRoute();

    onBeforeMount(() => {
      console.log("Before Mount");
      fetch(
        `http://www.omdbapi.com/?apikey=${env.apikey}&i=${route.params.id}&plot=full`
      )
        .then((response) => response.json())
        .then((data) => {
          movie.value = data;
        });
    });

    return {
      movie,
      route,
    };
  },
};
</script>

<style lang="scss">
.movie-bg {
  background-color: #2f3c53;
  width: 100%;
}

.movie-detail {
  padding: 50px;
  max-width: 550px;
  margin: 0 auto;

  h2 {
    color: #fff;
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 10px;
  }

  .featured-img {
    display: block;
    max-width: 500px;
    margin: 16px auto;
  }

  h3 {
    color: #e6af2e;
    font-size: 20px;
    margin: 10px 0;
  }

  p {
    color: #fff;
    font-size: 15px;
    line-height: 1.4;
    text-align: justify;
  }
}
</style>